<template>
    <div class="particle-box">
        <template v-for="(item,index) of particleList">
            <div
                :class="{'outer':true,'p1-outer':ani}"
                :style="outerStyleComputed(index)"
                :key="item.id"
            >
                <div :class="{'particle':true,'p1-inner':ani}" :style="innerStyleComputed(index)"></div>
            </div>
        </template>
    </div>
</template>
<script>
function randomFloat (min, max) {
  return Math.random() * (max - min) + min;
}
export default {
  name: "particle",
  data () {
    return {
      debug: true,
      particleList: [],
      ani: true,
      paused: true,
    }
  },
  computed: {
    outerStyleComputed () {
      return (index) => {
        let curParticle = this.particleList[index * 1];
        return {
          '--outer-end-y': `${curParticle.outer[0]}rem`,
          '--outer-end-scale': curParticle.outer[1],
          'animation-delay': `${curParticle.delay}s`,
          'animation-play-state': this.paused ? 'paused' : 'running',
        }
      }
    },
    innerStyleComputed () {
      return (index) => {
        let curParticle = this.particleList[index * 1];
        return {
          '--inner-center-x': `${curParticle.inner[0]}rem`,
          '--inner-end-x': `${curParticle.inner[1]}rem`,
          'animation-delay': `${curParticle.delay}s`,
          'animation-play-state': this.paused ? 'paused' : 'running',
        }
      }
    },
  },
  created () {
    this.initParticles();
  },
  methods: {
    initParticles (num = 20) {
      let particleList = [], i;
      if (this.debug) {
        particleList = [{ "id": "p0", "outer": [1.8548098989672726, 0.07212436938645782], "inner": [-0.47328813401195013, -1.6745751383660423], "delay": 0 }, { "id": "p1", "outer": [1.572895611673799, 0.09642186217782234], "inner": [-0.8521609352173254, -1.8211061309175132], "delay": 0.1 }, { "id": "p2", "outer": [1.6164942420273558, 0.08917714918685925], "inner": [0.514122158259751, 1.9800262039942949], "delay": 0.2 }, { "id": "p3", "outer": [1.76887823885555, 0.07733489061048285], "inner": [-0.48805546012611023, -1.7454761448442548], "delay": 0.30000000000000004 }, { "id": "p4", "outer": [1.7131941346284885, 0.07717011344398227], "inner": [-0.38648971373725205, -1.9212813367563522], "delay": 0.4 }, { "id": "p5", "outer": [1.7441177012423095, 0.10561234901149855], "inner": [-0.7910213271424369, -1.9971390084701546], "delay": 0.5 }, { "id": "p6", "outer": [1.5494037562000584, 0.012177210537870665], "inner": [-0.4359896891875854, -1.913391239175394], "delay": 0.6000000000000001 }, { "id": "p7", "outer": [1.8155214943368978, 0.1906877375991035], "inner": [0.6298622547258277, 1.5982630209241562], "delay": 0.7000000000000001 }, { "id": "p8", "outer": [1.7436448814831462, 0.10709520263822317], "inner": [-0.7899333690444035, -1.664894326264887], "delay": 0.8 }, { "id": "p9", "outer": [1.707231112917124, 0.1405275658047723], "inner": [0.5554981703793898, 1.7689348328272572], "delay": 0.9 }, { "id": "p10", "outer": [1.9098489413822008, 0.07426046697904147], "inner": [0.6572899947362902, 1.5986077389084263], "delay": 1 }, { "id": "p11", "outer": [1.8978860141534935, 0.09610828542161545], "inner": [-0.4242849876924897, -1.9524713123891728], "delay": 1.1 }, { "id": "p12", "outer": [1.7092547315810651, 0.09619175279140785], "inner": [-0.69160542656634, -1.7012419839053488], "delay": 1.2000000000000002 }, { "id": "p13", "outer": [1.687234727230896, 0.05192843169033554], "inner": [-0.01717914845209423, -1.8355566666315113], "delay": 1.3 }, { "id": "p14", "outer": [1.9510883860262258, 0.01825679168470682], "inner": [-0.9471867635447513, -1.6686914021333246], "delay": 1.4000000000000001 }, { "id": "p15", "outer": [1.7683920764423449, 0.11107981351302071], "inner": [-0.31810339643755414, -1.982531113339464], "delay": 1.5 }, { "id": "p16", "outer": [1.9270599126427401, 0.19506087242211692], "inner": [-0.196198372494238, -1.5732532621599278], "delay": 1.6 }, { "id": "p17", "outer": [1.5188475993201171, 0.12140134088827606], "inner": [0.8268813592048483, 1.9350691323014573], "delay": 1.7000000000000002 }, { "id": "p18", "outer": [1.8220806324331136, 0.17841216337625646], "inner": [-0.5946708609000542, -1.6210955731853032], "delay": 1.8 }, { "id": "p19", "outer": [1.8014199632426662, 0.07950611670919666], "inner": [0.07833976554298294, 1.8831308284867374], "delay": 1.9000000000000001 }];
      } else {
        for (i = 0; i < num; i++) {
          let inner_0 = randomFloat(-1, 1),
            inner_1 = (inner_0 > 0 && randomFloat(1.5, 2)) || randomFloat(-2, -1.5);
          particleList.push({
            id: `p${i}`,
            outer: [randomFloat(1.5, 2), randomFloat(0, 0.2)],
            inner: [inner_0, inner_1],
            delay: (i % num) * 0.1
          });
        }
      }
      this.particleList = particleList;
    },
  },
}
</script>
<style lang="scss" scoped>
.particle-box {
    position: relative;
}
.outer {
    display: flex;
    justify-content: center;
    align-items: center;
}
.particle {
    --particle-size: 0.4rem;
    width: var(--particle-size);
    height: var(--particle-size);
    background: rgba(238, 130, 238, 0.8);
    border-radius: 50%;
    position: absolute;
}
.p1-outer {
    animation: particle-outer-ani 1s ease-out infinite forwards;
}
.p1-inner {
    opacity: 0;
    animation: particle-inner-ani 1s linear infinite forwards;
}

@keyframes particle-outer-ani {
    0% {
        transform: translateY(0rem) scale(1);
    }
    100% {
        transform: translateY(var(--outer-end-y)) scale(var(--outer-end-scale));
    }
}

@keyframes particle-inner-ani {
    0% {
        opacity: 0;
        transform: translateX(0rem);
    }
    1% {
        opacity: 1;
        transform: translateX(0rem);
    }
    40% {
        opacity: 0.8;
        transform: translateX(var(--inner-center-x));
    }
    100% {
        opacity: 0;
        transform: translateX(var(--inner-end-x));
    }
}
</style>