<template>
    <div class="abs" :hidden="!show">
        <music-button @backgroundMusicPause="musicTapPaused" @backgroundMusicPlay="musicTapPlaying"></music-button>
        <bg-music-hint
            :showBgMusicHint="showBgMusicHint"
            @confirm="hintTapConfirm"
            @cancel="hintTapCancel"
        ></bg-music-hint>
    </div>
</template>
<script>
// utils
import navi from '../util/nav-controller.js';
import BGMusicSoundJs from '../util/bgMusicSoundJs.js';
import {wxShare} from '../util/wechat-share.js'
//components
import MusicButton from './base/MusicButton.vue';
import BgMusicHint from './base/BgMusicHint.vue';
export default {
  name: "music",
  components: {
    MusicButton,
    BgMusicHint
  },
  data () {
    return {
      show:false,
      backgroundMusicAutoPlay: false,//背景音频是否自动播放
      showBgMusicHint: false,//弹出 询问是否播放音频弹窗
      bgMusic: null,//音频控制对象
      userAllowPlaying: false,//用户同意播放音频
      beforeDocumentVisibilityMusicPlaying: false,//网页不可见之前 音频是播放的
    }
  },
  mounted () {
    //自动播放音频
    if (this.backgroundMusicAutoPlay) {
      this.initBackgroundMusic();
    }
    this.windowVisibility();
  },
  methods: {
    //wx环境自动播放音频，非wx环境需打开弹窗
    initBackgroundMusic () {
      // 初始化背景音乐
      // 这里在业务上，主要要区分微信和非微信的浏览器环境的区别。
      // 因为在微信环境里，微信提供了接口可以实现自动播放音乐。
      // 在其他环境里，尤其是移动端手机浏览器环境里，是不能实现自动播放音乐的，这里最起码的做法是弹窗示意用户，是否播放背景音乐
      this.bgMusic = new BGMusicSoundJs();
      if (navi.isWX) {
        document.addEventListener(
          'WeixinJSBridgeReady',
          () => {
            console.log('WeixinJSBridgeReady');
            this.bgMusic.load();
            window.store.setBackgroundMusicPlaying();
          },
          false
        );
        //微信环境下，默认用户允许音乐自动播放,不弹出弹窗
        this.userAllowPlaying = true;
        this.showBgMusicHint = false;
        wxShare();
      } else { 
        this.showBgMusicHint = true;
        this.userAllowPlaying = false;
      }

    },
    //点击音频图标暂停
    musicTapPaused () {
      window.store.setBackgroundMusicPause();
      this.bgMusic.pause();
    },
    //点击音频图标播放
    musicTapPlaying () {
      if (this.userAllowPlaying) {
        window.store.setBackgroundMusicPlaying();
        this.bgMusic.resume();
      } else {
        this.hintTapConfirm();
      }
    },
    //确认播放音频
    hintTapConfirm () {
      this.hintTapCancel();
      this.bgMusic.load();
      window.store.setBackgroundMusicPlaying();
      this.userAllowPlaying = true;
    },
    //取消播放音频
    hintTapCancel () {
      this.userAllowPlaying = false;
      this.showBgMusicHint = false;
    },
    //网页是否可见 => 音频是否继续播放
    windowVisibility () {
      document.addEventListener('visibilitychange', () => {
        //兼容ios微信手Q
        if (this.userAllowPlaying) {
          if (document.hidden) {
            //网页不可见
            if (window.store.state.backgrundMusicIsPlaying) {
              //音频播放中，暂停音频
              this.musicTapPaused();
              this.beforeDocumentVisibilityMusicPlaying = true;
            } else {
              //音频暂停中
              this.beforeDocumentVisibilityMusicPlaying = false;
            }
          } else {
            //网页可见
            if (this.beforeDocumentVisibilityMusicPlaying) {
              this.musicTapPlaying();
            }
          }
        }
      });
    },
  },
}
</script>
<style lang="scss" scoped>
</style>