<template>
    <base-page :show="show">
        <div class="page outer-box">
            <div class="inner-box" ref="scroll_ref">
                <div :style="mapBgComputed" class="map-img"></div>
            </div>
            <rocket ref="rocket_ref"></rocket>
            <div class="rocket-box abs">
                <div class="btn btn-begin" @click="startAni">{{bgAni.state?'暂停':'开始'}}</div>
                <div class="btn btn-reload">重新开始</div>
            </div>
        </div>
    </base-page>
</template>
<script>
//utils 
import navi from '../util/nav-controller.js';
import { Shake } from '../util/shake.js';
//page 
import BasePage from './base/BasePage.vue';
import Rocket from './base/rocket.vue';
export default {
  name: 'page1',
  components: {
    BasePage,
    Rocket
  },
  data () {
    return {
      show: false,
      ani: true,
      globalState: window.store.state,
      imgs: {
        map: process.env.BASE_URL + "imgs/map/map.png",
      },
      bgAni: {
        state: false,
        fps: 60,
        lastTime: 0,
        initScrollTop: 0,
      },
      shakeInstance: null,
      lastX: 0,
    }
  },
  computed: {
    mapBoxScrollTopComputed () {
      let { full_vw, ful_vh } = this.globalState.content;
      return Math.abs(ful_vh - full_vw * 10773 / 750);
    },
    mapBgComputed () {
      return {
        'background-image': `url(${this.imgs.map})`,
      }
    },
  },
  watch: {
    show (newVal) {
      if (newVal) {
        this.$nextTick(() => {
          this.$refs.scroll_ref.scrollTop = this.bgAni.initScrollTop;
          this.ani = true;
          this.animationFrame();
        });
      } else {
        this.ani = false;
      }
    },
  },
  created () {
    this.bgAni.initScrollTop = this.mapBoxScrollTopComputed;
    //navi.isIos
    //navi.isWX
    if (!navi.isPC) {
      this.shakeInstance = new Shake(navi.isIos, this);
      if (!this.shakeInstance.authorized) {
        //IOS 授权
        this.shakeInstance.authState();
      }
    }
  },
  methods: {
    //开始动画
    startAni () {
      this.bgAni.state = !this.bgAni.state;//背景开始移动
      this.$refs.rocket_ref.ani = this.bgAni.state;
    },
    //动画执行帧
    animationFrame () {
      if (!this.ani) {
        return;
      }
      let nowDate = Date.now();
      if ((nowDate - this.bgAni.lastTime) < (1000 / this.bgAni.fps)) {
        requestAnimationFrame(this.animationFrame);
        return;
      }
      this.bgAni.lastTime = nowDate;

      if (this.bgAni.state) {
        this.bgAniHandler();
      }

      requestAnimationFrame(this.animationFrame);
    },
    //背景移动
    bgAniHandler () {
      this.bgAni.initScrollTop -= 1;
      if (this.bgAni.initScrollTop <= 0) {
        this.bgAni.initScrollTop = 0;
        //动画结束
        this.ani = false;
      }
      this.$refs.scroll_ref.scrollTop = this.bgAni.initScrollTop;
    },
    //移动端 重力移动
    gravityMove (ev) {
      if ('undefined' != this.$refs.rocket_ref) {
        if (Math.abs(this.lastX - ev.x) > 0.6) {
          this.lastX = ev.x;
          this.$refs.rocket_ref.x = ev.x;
        }
      }
    },
  },
}
</script>
<style lang="scss" scoped>
img {
    object-fit: contain;
}
.outer-box {
    background: rgba(255, 255, 255, 0.8);
}
.inner-box {
    width: 8.5rem;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    position: absolute;
    left: 0;
    top: 0;

    .map-img {
        --map-width: 7.5rem;
        width: var(--map-width);
        height: calc(var(--map-width) * 10773 / 750);
        background-size: contain;
        position: relative;
        top: 0;
        left: 0;
    }
}

.rocket-box {
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.4);

    .btn {
        width: 2rem;
        height: 0.8rem;
        line-height: 0.8rem;
        font-size: 0.3rem;
        text-align: center;
        background: black;
        border-top-left-radius: 0.4rem;
        border-bottom-left-radius: 0.4rem;
        color: white;
        position: absolute;
        right: 0;

        &:active {
            background: #666;
            color: chartreuse;
        }
    }

    .btn-begin {
        bottom: 3rem;
    }

    .btn-reload {
        bottom: 2rem;
    }
}
</style>