<template>
    <base-page :show="show">
        <div class="page bg" :style="loadingStyleComputed">Loading {{ showLoadPercent }}%</div>
    </base-page>
</template>

<script>
import BasePage from './base/BasePage.vue';
import imageLoad from '../util/image-load.js';
export default {
  name: 'pageLoading',
  components: {
    BasePage,
  },
  data () {
    return {
      show: false,
      sharedState: window.store.state,
      imageBgSrc: process.env.BASE_URL + 'imgs/test/12.jpg',
      realLoadPercent: 0,//真实的loading进度
      showLoadPercent: 0,//显示的loading进度
      fps: 60,
      lastTime: 0,
    };
  },
  computed: {
    loadingStyleComputed () {
      return {
        'background-image': `url(${this.imageBgSrc})`,
      };
    },
  },
  created () {
    let _imageLoad = new imageLoad();
    _imageLoad
      .queueImage(this.sharedState.sourceArrayPreLoad)
      .imageLoadingProgressCallback(
        num => {
          this.preloadPercent = Math.floor(num);
        },
        () => {
          this.startLoading();
        }
      );
  },
  methods: {
    startLoading () {
      this.show = true;
      this.loadingAni();
      this.$emit('loadingLoadedComplete');
      let _imageLoad = new imageLoad();
      _imageLoad
        .queueImage(this.sharedState.sourceArrayLoading)
        .imageLoadingProgressCallback(
          num => {
            this.realLoadPercent = Math.floor(num);
          },
          () => {
            this.realLoadPercent = 100;
          }
        );
    },
    loadingAni () {
      if (!this.show) {
        return;
      }
      let nowTime = Date.now();
      if ((nowTime - this.lastTime) < (1000 / this.fps)) {
        requestAnimationFrame(this.loadingAni);
        return;
      }
      this.lastTime = nowTime;

      if (this.showLoadPercent >= 100 && this.realLoadPercent >= 100) {
        this.$emit('pageLoadedComplete');
        return;
      }
      this.showLoadPercent += 1;
      requestAnimationFrame(this.loadingAni);
    },
  },
};
</script>

<style lang="scss" scoped>
.bg {
    background-size: cover;
    background-position: center;
    font-size: 0.2rem;
}
</style>
