let store = {
    debug: false,
    state: {
        message: "Hello!",
        // 用于加载Loading页的素材列表
        sourceArrayPreLoad: [
            process.env.BASE_URL + "imgs/test/12.jpg",
            process.env.BASE_URL + "imgs/test/music-open.png",
        ],
        // 用于加载主内容的素材列表
        sourceArrayLoading: [
            process.env.BASE_URL + "imgs/map/map.png",
            process.env.BASE_URL + "imgs/rocket/01-top.png",
            process.env.BASE_URL + "imgs/rocket/02-center.png",
            process.env.BASE_URL + "imgs/rocket/03-sun-wing.png",
            process.env.BASE_URL + "imgs/rocket/04-bosster.png",
            process.env.BASE_URL + "imgs/rocket/05-rocket.png",
            process.env.BASE_URL + "imgs/rocket/06-bosster.png",
        ],
        content: {
            full_vw: 0,
            ful_vh: 0,
        },
        backgrundMusicIsPlaying: false
    },
    setBackgroundMusicPause() {
        this.state.backgrundMusicIsPlaying = false;
    },
    setBackgroundMusicPlaying() {
        this.state.backgrundMusicIsPlaying = true;
    },
    setContent(obj) {
        let docEl = document.documentElement,
            rem = (obj.vw * 1) / 7.5;
        if (obj && obj['vw'] && obj['vh']) {
            docEl.style.fontSize = `${rem}px`;
            this.state.content = {
                full_vw: obj.vw,
                ful_vh: obj.vh,
            }
        }
    },
};

let handler = {
    get: function(target, prop) {
        return Reflect.get(target, prop);
    },
    set: function(target, prop, value) {
        if (store.debug) {
            console.log(`store set: prop: ${prop}, value: ${value}`);
        }
        return Reflect.set(...arguments);
    }
};

let initStore = function(obj) {
    for (let [key, value] of Object.entries(obj)) {
        if (typeof value == "object") {
            obj[key] = new Proxy(value, handler);
            initStore(value);
        }
    }

    return new Proxy(obj, handler);
};

store.state = initStore(store.state);

window.store = store;

export default store;