class Shake {
    authorized = false; //已授权摇一摇
    app = null;

    constructor(isAuth, app) {
        console.log("isAuth ", isAuth);
        this.authorized = !isAuth;
        this.app = app;
        //不需要授权 isAuth=false =》 已授权摇一摇 authorized=true
        //需要授权 isAuth=true  => 已授权摇一摇 authorized=false
        this.authState();
    }

    //授权状态
    authState = () => {
        if (this.authorized) {
            //已授权
            this.shakeListener();
        } else {
            //需要授权
            this.askShake();
        }
    }

    //ios 摇一摇
    askShake() {
        console.log('ios 请求摇一摇权限');
        try {
            window.DeviceMotionEvent.requestPermission()
                .then(response => {
                    if (response == 'granted') {
                        this.authorized = true; //已授权
                        window.addEventListener(
                            'devicemotion',
                            this.deviceMotionHandler,
                            false
                        );
                    }
                })
                .catch(e => {
                    console.log('ios 没有获取许可');
                    console.error(e);
                    this.authorized = false; //未授权
                });
        } catch (ex) {
            console.log('ios设备运动许可失败！ 没有这样的许可');
            console.error(ex);
            this.authorized = false; //未授权
        }
    }

    //android 摇一摇
    shakeListener = () => {
        if (window.DeviceMotionEvent) {
            this.authorized = true; //已授权
            window.addEventListener(
                'devicemotion',
                this.deviceMotionHandler,
                false
            );
            console.log('Android摇一摇,监听时间添加成功');
        } else {
            console.log('Android摇一摇,监听时间添加失败');
            this.authorized = false; //未授权
        }
    }

    //摇一摇数据
    deviceMotionHandler = (ev) => {
        const { x, y } = ev.accelerationIncludingGravity;
        this.app.gravityMove({ x, y });
    }
}
export {
    Shake
}