<template>
    <div class="page abs rocket-box">
        <div class="rocket-move" :style="rocketMoveStyleComputed">
            <div class="flex-center">
                <img :src="rocket.center_02" class="center" />
                <img :src="rocket.top_01" class="top" />
                <img :src="rocket.bosster_04" class="bosster_04" />
                <img :src="rocket.rocket_05" class="rocket" />
                <img :src="rocket.bosster_06" class="bosster-06 bosster-center" />
                <particle class="particle-box par-center" ref="par_center_ref"></particle>
                <img :src="rocket.bosster_06" class="bosster-06 bosster-left" />
                <particle class="particle-box par-left" ref="par_left_ref"></particle>
                <img :src="rocket.bosster_06" class="bosster-06 bosster-right" />
                <particle class="particle-box par-right" ref="par_right_ref"></particle>
            </div>
        </div>
    </div>
</template>
<script>
import Particle from './particle.vue';
export default {
  name: 'rocket',
  components: {
    Particle
  },
  data () {
    return {
      ani: false,
      x: 0,
      rocket: {
        top_01: process.env.BASE_URL + "imgs/rocket/01-top.png",
        center_02: process.env.BASE_URL + "imgs/rocket/02-center.png",
        sun_wing_03: process.env.BASE_URL + "imgs/rocket/03-sun-wing.png",
        bosster_04: process.env.BASE_URL + "imgs/rocket/04-bosster.png",
        rocket_05: process.env.BASE_URL + "imgs/rocket/05-rocket.png",
        bosster_06: process.env.BASE_URL + "imgs/rocket/06-bosster.png",
      },
    }
  },
  computed: {
    rocketMoveStyleComputed () {
      return {
        'transform': `translateX(${-3.75 * this.x / 10}rem)`
      }
    }
  },
  watch: {
    ani (newVal) {
      if (newVal) {
        this.$refs.par_center_ref.paused = false;
        this.$refs.par_left_ref.paused = false;
        this.$refs.par_right_ref.paused = false;
      } else {
        this.$refs.par_center_ref.paused = true;
        this.$refs.par_left_ref.paused = true;
        this.$refs.par_right_ref.paused = true;
      }
    },
  },
  methods: {

  },
}
</script>
<style lang="scss" scoped>
.rocket-box {
    background: rgba(0, 0, 0, 0.8);
}

.rocket-box,
.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.flex-center {
    position: relative;
    transform: translateY(-3rem);
    --center-width: 0.8rem;
    --top-width: 0.8rem;
    --bosster-04-width: 0.8rem;
    --rocket-width: 0.8rem;
    --bosster-06-width: 0.56rem;
}

img {
    object-fit: contain;
}

.center {
    max-height: calc(160 * 7.5rem / 750);
    width: var(--center-width);
    height: calc(var(--center-width) * 160 / 154);
    position: absolute;
}

.top {
    max-height: calc(118 * 7.5rem / 750);
    width: var(--top-width);
    height: calc(var(--top-width) * 118 / 156);
    position: absolute;
    top: calc(
        -1 * var(--top-width) * 118 / 156 - var(--center-width) * 160 / 154 / 2
    );
}

.bosster_04 {
    max-height: calc(119 * 7.5rem / 750);
    width: var(--bosster-04-width);
    height: calc(var(--bosster-04-width) * 119 / 155);
    position: absolute;
    top: calc(var(--center-width) * 160 / 154 / 2);
}
.rocket {
    max-height: calc(340 * 7.5rem / 750);
    width: var(--rocket-width);
    height: calc(var(--rocket-width) * 340 / 155);
    position: absolute;
    top: calc(
        var(--center-width) * 160 / 154 / 2 + var(--bosster-04-width) * 119 /
            155
    );
}

.bosster-06 {
    max-height: calc(348 * 7.5rem / 750);
    width: var(--bosster-06-width);
    height: calc(var(--bosster-06-width) * 348 / 108);
    position: absolute;
    top: calc(
        var(--center-width) * 160 / 154 / 2 + var(--bosster-04-width) * 119 /
            155 + var(--rocket-width) * 340 / 155 * 0.1
    );
}
.particle-box {
    width: 10px;
    height: 10px;
    background: red;
    position: absolute;
    top: calc(
        var(--center-width) * 160 / 154 / 2 + var(--bosster-04-width) * 119 /
            155 + var(--rocket-width) * 340 / 155 * 1.2
    );
}

.bosster-center {
    opacity: 1;
}
.par-center {
    opacity: 1;
}
.bosster-left {
    left: -1rem;
}
.par-left {
    left: calc(-0.72rem - 5px);
}
.bosster-right {
    right: -1rem; //0.96 + 0.4
}
.par-right {
    right: calc(-0.72rem - 5px);
}
</style>