<template>
    <div id="app" :style="appSizeComputed" ref="app_ref">
        <swiper ref="mySwiper" :options="swiperOptions" class="content">
            <swiper-slide>
                <page-1 ref="page1_ref"></page-1>
                <page-loading
                    ref="loading_ref"
                    @loadingLoadedComplete="loadingLoadComplete"
                    @pageLoadedComplete="pageLoadComplete"
                ></page-loading>
                <music-component ref="music_ref"></music-component>
            </swiper-slide>
        </swiper>
        <screen-rotate ref="screen_rotate_ref"></screen-rotate>
    </div>
</template>

<script>
// utils
import navi from './util/nav-controller.js';
import { getWindowSize } from './util/rem.js';
// swiper
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/swiper-bundle.css'
// pages 
import ScreenRotate from './components/base/screen-rotate.vue';
import PageLoading from './components/PageLoading.vue';
import MusicComponent from './components/Music.vue';
import Page1 from './components/Page1.vue';
export default {
  name: 'app',
  components: {
    Swiper,
    SwiperSlide,
    ScreenRotate,
    PageLoading,
    MusicComponent,
    Page1
  },
  data () {
    return {
      globalStore: window.store,
      vh: 0,
      swiperOptions: {
        direction: 'vertical',
        observer: true,
        observeParents: true,
        observeSlideChildren: true,
        touchRatio: 0,
        resistanceRatio: 0,
      }
    };
  },
  computed: {
    appSizeComputed () {
      return {
        '--px-content-height': `${this.vh}px`
      }
    },
  },
  created () {
    this.resize();
    window.onorientationchange = this.showScreenRotatePage;
  },
  mounted () {
    this.showScreenRotatePage();
  },
  methods: {
    resize () {
      // { width, height } = getWindowSize()
      //{ width, height } =this.$refs.app_ref.getBoundingClientRect();
      let vw = 0, vh = 0, { width: w0, height: h0 } = getWindowSize();
      vw = (w0 > h0 && h0) || w0;
      vh = (w0 > h0 && w0) || h0;
      console.log("app", { w0, h0 });
      console.log("result", { vw, vh });
      if (navi.isPC) {
        vh = h0 * 0.8;
        vw = vh * 9 / 18;
      } else if (navi.isIpad) {
        vw = vh * 9 / 18;
      }
      this.vh = vh;
      this.globalStore.setContent({ vw, vh });
    },
    resizeHandler () {
      if ('undefined' == typeof this.$refs.screen_rotate_ref || this.$refs.screen_rotate_ref.show) {
        console.log("screen_rotate_ref ---- false ");
      } else {
        setTimeout(() => {
           console.log("Listener ---- resize ");
          this.resize();
        }, 3000);
      }
    },
    //旋转屏幕
    showScreenRotatePage () {
      if (window.orientation == 0 || window.orientation == 180) {
        //竖屏
        this.$refs.screen_rotate_ref.show = false;
      } else {
        //非竖屏
        if (navi.isMobile || navi.isIpad) {
          this.$refs.screen_rotate_ref.show = true;
        } else {
          this.$refs.screen_rotate_ref.show = false;
        }
      }
      this.resizeHandler();
    },
    //loading页所需素材加载完成
    loadingLoadComplete () {
      this.showMusic();
    },
    //page页所需素材加载完成
    pageLoadComplete () {
      this.showPage1();
    },
    //show music
    showMusic () {
      this.$refs.music_ref.show = true;
    },
    //show page1
    showPage1 () {
      this.$refs.loading_ref.show = false;
      this.$refs.page1_ref.show = true;
    },
  },
};
</script>

<style lang="scss">
* {
    box-sizing: border-box;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
}
body {
    -webkit-text-size-adjust: 100% !important;
}
#app {
    --px-content-width: 7.5rem;
    --px-content-height: 100vh;
    width: 100vw;
    height: 100%;
    position: fixed;
    overflow: hidden;
    background: whitesmoke;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    flex-shrink: 0;
    width: var(--px-content-width);
    height: var(--px-content-height);
    position: relative;
    overflow: hidden;
    background: transparent;
}

.page {
    width: var(--px-content-width);
    height: var(--px-content-height);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.abs {
    position: absolute;
    left: 0;
    top: 0;
}
</style>
